import { Component, Input, OnInit } from '@angular/core';
import momentTimezone, { Duration } from 'moment-timezone';

@Component({
    selector: 'app-tz-slider',
    templateUrl: './tz-slider.component.html',
    styleUrls: ['./tz-slider.component.scss'],
})
export class TzSliderComponent implements OnInit {
    @Input() timezone: string;
    @Input() base: string;
    @Input() enabled: boolean;

    interval: number = 60 * 60;
    seconds: number = 60 * 60 * 24 - 1;
    hours: number[] = [];

    private baseTz: momentTimezone.Moment;
    private curTz: momentTimezone.Moment;
    curTime: number;

    constructor() { }

    ngOnInit(): void {
        this.baseTz = momentTimezone.tz(this.base);
        this.curTz = momentTimezone.tz(this.timezone);
        const baseStart: momentTimezone.Moment = this.baseTz.clone().hours(0).minutes(0).seconds(0);
        const curStart: momentTimezone.Moment = this.curTz.clone().hours(0).minutes(0).seconds(0);
        this.curTime = Math.abs(this.curTz.unix() - baseStart.unix());
        const hours: number = baseStart.diff(curStart, 'hours');
        for (let i = 0; i < 24; i += 1) {
            this.hours.push((i + hours) % 24);
        }
    }

    getTime(value: number): string {
        const time: Duration = momentTimezone.duration(value, 'seconds');
        const hour: number = time.hours();
        const min: number = time.minutes();
        return min < 10 ? `${hour}:0${min}` : `${hour}:${min}`;
    }
}
