import { Component, OnInit } from '@angular/core';

import momentTimezone from 'moment-timezone';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit {
    private currentTimezone: string;

    // title = 'timezone';
    // supportedTimezones: string[];
    timezones: string[];
    hours: number[] = [];

    ngOnInit(): void {
        // this.supportedTimezones = momentTimezone.tz.names();
        this.currentTimezone = momentTimezone.tz.guess(true);
        this.timezones = this.getSavedTz();

        let i: number;
        for(i = 0; i < 24; i += 1) {
            this.hours.push(i);  // 24hr a day
        }
    }

    private getSavedTz(): string[] {
        const tz: string = localStorage.getItem('chosenTimezone');
        return tz ? JSON.parse(tz) : [this.currentTimezone];
    }

    private saveTz(): void {
        localStorage.setItem('chosenTimezone', JSON.stringify(this.timezones));
    }

    add(): void {
        this.timezones.push(this.currentTimezone);
        this.saveTz();
    }

    reset(): void {
        localStorage.clear();
        this.timezones = this.getSavedTz();
    }

    updateTime(tz: string, idx: number): void {
        if (tz.length) {
            this.timezones[idx] = tz;
        } else {
            this.timezones.splice(idx, 1);
        }
        this.saveTz();
    }

    // getTime(tz: string, hour: number): string {
    //     const currentTime: momentTimezone.Moment = momentTimezone.tz(tz);
    //     const newTime: momentTimezone.Moment = momentTimezone.tz(tz).add(hour, 'hours');
    //     let time: string = newTime.format('ha');

    //     if (currentTime.format('DD') !== newTime.format('DD')) {
    //         time = `(+1) ${time}`;
    //     }
    //     return time;
    // }
}
