import { Component, EventEmitter, Input, Output, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import momentTimezone from 'moment-timezone';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';

@Component({
    selector: 'app-tz-selection',
    templateUrl: './tz-selection.component.html',
    styleUrls: ['./tz-selection.component.scss'],
})
export class TzSelectionComponent implements OnInit {
    @Input() timezone: string;
    @Output() tzEmitter: EventEmitter<string> = new EventEmitter<string>();

    private allTimezones: string[] = momentTimezone.tz.names();
    tzFormControl: FormControl;
    filteredTimezones: Observable<string[]>;

    constructor() { }

    ngOnInit(): void {
        this.tzFormControl = new FormControl(this.timezone);
        this.filteredTimezones = this.tzFormControl.valueChanges
            .pipe(
                startWith(''),
                map((tz: string) => {
                    return tz ? this.filterTimezoneByInput(tz) : this.allTimezones.slice()
                }),
            );
    }

    private filterTimezoneByInput(value: string): string[] {
        const filterValue = value.toLowerCase();
    
        return this.allTimezones.filter((tz: string) => tz.toLowerCase().indexOf(filterValue) !== -1);
    }

    getCurrentTime(timezone: string): string {
        return momentTimezone.tz(timezone).format('llll');
    }

    updateTime(): void {
        this.timezone = this.tzFormControl.value;
        this.tzEmitter.emit(this.tzFormControl.value);
    }

    removeTime(): void {
        this.tzEmitter.emit('');
    }
}
